.paragraphContainer {
  border-radius: 1.5625rem;
  height: 6.25rem;
  background-color: transparent;
}
.titleSection {
  position: absolute;
  top: 2.09375rem;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 40%;
  padding: 0.625rem;
  margin-top: -2.03125rem;
  border-radius: 1.5625rem;
  background-color: rgba(31, 78, 121, 0.936);
  box-shadow: 0.375rem 0.375rem 0.375rem 0.0625rem rgba(31, 78, 121, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.625rem;
}
.aboutParagraph {
  padding: 1.25rem;
  padding-top: 3.875rem;
  opacity: 0;
  transform: translateX(200%);
}
.projectsContainer,
.skillsContainer {
  width: inherit;
  padding: 1.25rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.25rem;
  overflow: hidden;
  transform: translateX(-200%);
}
.project {
  position: relative;
  min-height: 100%;
  padding: 0.9375rem;
  border-radius: 1.5625rem;
  box-shadow: 0.75rem 0.75rem 1.25rem 0.0625rem rgba(31, 78, 121, 0.8);
  background-color: rgba(31, 78, 121, 0.93);
}
.imgContainer {
  width: 70%;
  height: 7.5rem;
  margin: auto;
  overflow: hidden;
}
.projectIcon {
  width: 90%;
  height: 6.25rem;
  padding-top: 1.5625rem;
  display: flex;
  object-fit: contain;
}
.projectTitle {
  width: 13.75rem;
  /* font-weight: bold; */
  margin: 0.625rem auto 3.125rem auto;
  padding: 0.9375rem 1.875rem;
  text-align: center;
  border-radius: 0.625rem;
  background-color: rgba(38, 108, 174, 0.936);
  object-fit: contain;
}
.pragraphTitle {
  font-weight: bold;
  text-align: center;
  padding: 0.3125rem 0rem;
  margin-bottom: 1.875rem;
  border-radius: 0.625rem;
  background-color: rgba(38, 108, 174, 0.936);
}
.projectDescription {
  height: 8.125rem;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 0rem 1.25rem;
  margin-bottom: 1.875rem;
  /* overflow-x: scroll;
	overflow-y: scroll; */
}
.projectObjectiveUl {
  padding: 0rem 1.25rem;
  margin-bottom: 3.4375rem;
  list-style-type: none;
  /* overflow-x: scroll;
	overflow-y: scroll; */
}
.projectObjectiveLi {
  padding-bottom: 0.625rem;
  display: flex;
  list-style-type: none;
}
.projectObjectiveLi span {
  width: 80%;
}
.arrowStyle {
  /* margin-right: 1.25rem; */
  /* padding-top: 0.9375rem; */
  width: 2.5rem;
  font-size: 1.25rem;
}
.projectObjectiveWarningContainer {
  margin-bottom: 3.4375rem;
}
.projectObjectiveWarningUl {
  margin-bottom: 0.625rem;
  padding: 0rem 1.25rem;
}
.warningStyle {
  font-size: 1.5625rem;
  margin-right: 0.1875rem;
}
.warning {
  display: flex;
  font-size: 0.875rem;
}
.warning i {
  margin-top: 0rem;
}
.warning span {
  padding-top: 0.3125rem;
  padding-left: 0.3125rem;
}
.linkContainer {
  position: absolute;
  width: 80%;
  height: 2.5rem;
  display: flex;
  justify-content: space-around;
  left: 50%;
  transform: translate(-50%, -50%);
  bottom: 0rem;
}
.linkContainer :hover {
  scale: 1.3;
  border: 0.1875rem solid white;
}
.projectLink {
  z-index: 1;
  width: 4.375rem;
  height: 2.5rem;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0.625rem;
  background-color: rgba(38, 108, 174, 0.936);
}
.iconsLanguageUl {
  width: 10rem;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 0.9375rem;
  padding: 0.625rem 0rem;
  background-color: rgba(31, 78, 121, 0.6);
  box-shadow: 0.75rem 0.75rem 1.25rem 0.0625rem rgba(31, 78, 121, 0.8);
}
.languageIcons {
  width: 4.375rem;
}
.iconsName {
  font-weight: bold;
  margin-bottom: 0.9375rem;
}
:root {
  --progress: rgba(38, 108, 174, 0.936);
}
.container {
  width: 100%;
  margin: 0rem auto;
}
.container p {
  text-align: center;
  margin-bottom: 0.4375rem;
}
.progressbarWrapper {
  width: 80%;
  height: 0.625rem;
  border-radius: 0.9375rem;
  background-color: #dfe6e9;
}
.progressbar {
  width: 0;
  height: inherit;
  background-color: var(--progress);
  border-radius: 0.9375rem 0rem 0rem 0.9375rem;
  margin: 0rem;
}

/* Début version Mobile jusqu'à 540px*/
@media all and (max-width: 540px) {
  .skillsContainer {
    grid-template-columns: repeat(2, 1fr);
  }
  .iconsLanguageUl {
    width: 7rem;
  }
  .languageIcons {
    width: 2.375rem;
  }
  .iconsName {
    margin-bottom: 0rem;
  }
}
/* Fin version Mobile jusqu'à 540px*/

/* Début version Mobile de 540px à 768px*/
@media all and (min-width: 541px) and (max-width: 768px) {
  .skillsContainer {
    grid-template-columns: repeat(3, 1fr);
  }
  .iconsLanguageUl {
    width: 7rem;
  }
  .languageIcons {
    width: 2.375rem;
  }
  .iconsName {
    margin-bottom: 0rem;
  }
}
/* Fin version Mobile de 540px à 768px*/

/* Début version Mobile jusqu'à 768px*/
@media all and (max-width: 768px) {
  .titleSection {
    width: 70%;
    border-radius: 1.25rem;
  }
  .wrapper {
    margin-bottom: 0rem;
  }
  .aboutParagraph {
    padding-top: 1.875rem;
  }
  .aboutParagraph0 {
    height: 12.5rem;
    overflow-y: scroll;
  }
  .projectsContainer {
    width: 95%;
    max-width: 31.25rem;
    padding-top: 0rem;
    justify-content: center;
    grid-template-columns: repeat(1, 1fr);
  }
  .projectDescription {
    height: auto;
  }
  .projectIcon {
    width: 90%;
  }
}
/* Fin version Mobile jusqu'à 768px*/

/* Début version de 769px à 1024px*/
@media all and (min-width: 769px) and (max-width: 1024px) {
  .wrapper {
    margin-bottom: 0rem;
  }
}
/* Fin version de 769px à 1024px*/
